import { Component, OnInit } from '@angular/core'
import { SpachaMapService } from './spacha-map/spacha-map.service'

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
    title = 'app works!';
    private lng: number = 0
    private lat: number = 0

    constructor(private mapService: SpachaMapService) {}

    ngOnInit() {
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(position => {
                this.lng = position.coords.longitude
                this.lat = position.coords.latitude
            })
        }
    }

    driverLicks() {
        const ref = localStorage.getItem('driverInterest')
        const data = {
            location: { longitude: this.lng, latitude: this.lat },
            createdOn: new Date().toString()
        }

        if (!ref) {
            const appRef = this.mapService.driverDemand(data)
            localStorage.setItem('driverInterest', appRef)
            console.log(appRef)
        }
    }
}



